.home-title {
	@include fs(20rem);
	line-height: 24px;
	// font-weight: 700
	text-transform: capitalize;
	padding: 0 0 10px;
	@include pos(rel,null);
	border-bottom: 1px solid darken($white,5%);
	&:after {
		content: '';
		display: block;
		width: 50px;
		height: 2px;
		background: $blue;
		@include pos(abs, null null -1px 0);
		z-index: 3; }
	@include media-breakpoint-down(lg) {
		@include fs(16rem);
		line-height: 1.3; }
	@include media-breakpoint-down(sm) {
		@include fs(16rem); } }
.page-title {
	@include fs(24rem);
	// font-weight: 700
	margin-bottom: 15px;
	text-transform: capitalize;
	border-bottom: 1px solid darken($white,5%);
	@include media-breakpoint-down(sm) {
		@include fs(16rem); }
	&.big {
		@include fs(32rem);
		@include media-breakpoint-down(sm) {
			@include fs(24rem); } } }
.home-video {
	margin: 30px 0 30px;
	// height: calc(100% - 50px)
	@include bor-rad(5px);
	overflow: hidden;
	@include pos(rel,null);
	border: 2px solid darken($white,5%);
	padding: 20px;
	@include media-breakpoint-down(lg) {
		padding: 15px; }
	@include media-breakpoint-down(md) {
		margin: 0 0 20px;
		height: auto; }
	.home-title {
		margin-bottom: 20px; }
	.video-wrap {
		margin: 0px -20px;
		overflow: hidden;
		.video-des {
			color: $main;
			.video-name {
				@include fs(13rem);
				font-weight: 500;
				@include textover(2); }
			.video-view {
				@include fs(12rem);
				opacity: 0.8;
				font-weight: 300; } }
		.video-big {
			.video-des {
				padding: 15px;
				background: $mainblue; } }
		.video-list {
			background: $white;
			height: 377px;
			overflow: auto;
			padding: 0px 20px;
			&::-webkit-scrollbar-track {
				background-color: transparent;
				padding: 2px; }
			&::-webkit-scrollbar {
				width: 5px;
				background-color: transparent; }
			&::-webkit-scrollbar-thumb {
				background-color: color-rgba($black,0.3); }
			@include media-breakpoint-down(lg) {
				height: 351px; }
			@include media-breakpoint-down(md) {
				height: 250px; }
			.video {
				display: block;
				+ .video {
					margin-top: 25px; }
				@include transition(all,0.5s);
				@include pos(rel,null);
				// border-bottom: 1px dashed darken($white,10%)
				@include media-breakpoint-down(md) {
					width: 50%;
					// border-right: 1px dashed darken($white,10%)
					&:nth-child(2n) {
						border-right: none; } }
				@include media-breakpoint-down(sm) {
					width: 100%;
					border-right: none;
					&:first-child {
						border-top: none; }
					&:last-child {
						border-bottom: none; } }
				&:before {
					content: '';
					@include pos(abs, 0 null 0 0);
					width: 3px;
					background: $bluegradient-v;
					opacity: 0;
					@include transition(all,0.5s); }
				figure {
					@include flexbox;
					align-items: flex-start;
					align-content: flex-start;
					.video-img {
						width: 90px;
						@include pos(rel,null);
						&:before {
							content: '\f04b';
							background: $white;
							color: $blue;
							@include fs(12rem);
							width: 32px;
							height: 32px;
							@include bor-rad(100px);
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							@include pos(abs,50% null null 50%);
							@include transform(translate(-50%,-50%));
							@include transition(all,0.5s); } }
					figcaption {
						width: calc(100% - 90px);
						padding-left: 10px; } }
				&:hover {
					figure {
						.video-img {
							&:before {
								background: $blue;
								color: $white; } }
						figcaption {
							.video-name {
								color: $blue; } } } } } } } }
.btn-viewall {
	@include pos(abs, 23px 20px null null);
	@include fs(12rem);
	text-transform: uppercase;
	color: darken($white,50%);
	@include flexbox;
	align-items: center;
	align-content: center;
	@include media-breakpoint-down(lg) {
		top: 20px; }
	@include media-breakpoint-down(sm) {
		top: 16px; }
	&:hover {
		color: $blue; } }

.home-qc, .home-qc2 {
	margin-bottom: 40px;
	@include media-breakpoint-down(lg) {
		margin-bottom: 0; }
	@include media-breakpoint-down(sm) {
		display: none; }
	.qc-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -10px;
		@include media-breakpoint-down(md) {
			display: block;
			white-space: nowrap;
			overflow: auto;
			overflow-y: hidden;
			margin: 0px; } }
	.item {
		width: 25%;
		padding: 0px 10px;
		@include media-breakpoint-down(md) {
			display: inline-block;
			vertical-align: top;
			width: 280px;
			padding: 0px;
			+ .item {
				margin-left: 10px; } }
		@include media-breakpoint-down(sm) {
			width: 180px;
			display: inline-block;
			vertical-align: top; } }
	.banner-qc {
		display: block;
		@include bor-rad(5px);
		overflow: hidden;
		margin-bottom: 0px;
		// box-shadow: 0px 5px 20px color-rgba($black,0.05)
		@include media-breakpoint-down(md) {
			margin-bottom: 15px; }
		&:hover {
			filter: brightness(120%);
 } } }			// box-shadow: 0px 5px 20px color-rgba($black,0.3)
.home-qc2 {
	@include media-breakpoint-down(lg) {
		margin-bottom: 15px; }
	.qc-list {
		.item {
			width: 50%;
			padding: 0px 10px;
			@include media-breakpoint-down(md) {
				width: 50%; }
			@include media-breakpoint-down(sm) {
				width: 180px;
				display: inline-block;
				vertical-align: top; } } } }

.home-category {
	margin-bottom: 30px;
	@include pos(rel,null);
	border-top: 1px solid darken($white,5%);
	padding: 40px 0 0;
	@include media-breakpoint-down(lg) {
		padding: 0; }
	@include media-breakpoint-down(md) {
		margin: 0;
		border-top: none; }
	.home-title {
		margin-bottom: 20px;
		display: none; }
	.btn-viewall {
		display: none; }
	.category-wrap {
		overflow: hidden;
		@include media-breakpoint-down(lg) {
			padding: 0;
			@include pos(rel,null); }
		@include media-breakpoint-down(sm) {
			box-shadow: none;
			padding: 0;
			overflow: visible; } }
	.category-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -5px;
		@include media-breakpoint-down(md) {
			display: block;
			overflow: auto;
			white-space: nowrap;
			padding: 0px 0; }
		@include media-breakpoint-down(sm) {
			padding: 0 15px;
			margin: 0px -15px; }
		.item {
			width: calc(100% / 8);
			padding: 0px 5px;
			@include media-breakpoint-down(md) {
				width: 170px;
				display: inline-block; }
			@include media-breakpoint-down(sm) {
				padding: 0;
				width: 120px;
				margin-bottom: 15px;
				+ .item {
					margin-left: 5px; } }
			.category-item {
				display: block;
				@include pos(rel,null);
				@include bor-rad(5px);
				overflow: hidden;
				.img {
					img {
						width: 100%; } }
				.name {
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: color-rgba($black,0.1);
					@include flexbox;
					z-index: 3;
					align-items: center;
					align-content: center;
					justify-content: center;
					text-align: center;
					text-shadow: 1px 1px 1px $main;
					@include transition(all,0.5s);
					span {
						padding-left: 10px;
						@include fs(16rem);
						font-weight: 500;
						color: $white;
						line-height: 1.2;
						@include media-breakpoint-down(lg) {
							@include fs(13rem);
							font-weight: normal; }
						@include media-breakpoint-down(sm) {
							@include fs(12rem); } } }
				&:hover {
					.name {
						background: color-rgba($blue,0.8); } } } } } }

.home-hotsale {
	@include pos(rel,null);
	margin-bottom: 40px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 15px; }
	.tab-content {
		background: $white;
		overflow: hidden;
		@include media-breakpoint-down(sm) {
			padding: 0;
			box-shadow: none;
			overflow: visible; } }
	.btn-wrap {
		text-align: center; }
	.nav-tabs {
		margin-bottom: 20px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 15px; } }
	.btn-viewall {
		@include pos(static, null);
		@extend .btn-outline;
		@include media-breakpoint-down(sm) {
			@include fs(13rem); }
		&:after {
			display: none; } }
	.product-slick {
		margin-bottom: 20px;
		@include media-breakpoint-down(sm) {
			overflow: auto;
			white-space: nowrap;
			margin: 0px -15px;
			padding: 0px 15px; }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 1;
			@include fs(0);
			// color: $white
			text-shadow: 0px 1px 5px color-rgba($black,0.1);
			opacity: 0;
			@include transition(all,0.5s);
			&:before {
				content: '';
				@include fs(14rem);
				@include flexbox;
				width: 36px;
				height: 36px;
				justify-content: center;
				align-items: center;
				@include bor-rad(50px);
				background: $white;
				border: 1px solid darken($white,5%); }
			&:hover {
				&:before {
					background: $blue;
					color: $white;
					border-color: $blue; } } }
		.slick-prev {
			left: 0px;
			&:before {
				content: '\f177'; } }
		.slick-next {
			right: 0px;
			&:before {
				content: '\f178'; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; }
			.slick-prev {
				left: 10px; }
			.slick-next {
				right: 10px; } }
		.slick-track {
			padding: 15px 0; }
		.slick-list {
			margin: 0px -10px; }
		.item {
			height: 100%;
			padding: 0px 10px;
			@include media-breakpoint-down(sm) {
				display: inline-block;
				vertical-align: top;
				width: auto;
				width: 150px;
				margin-bottom: 15px;
				padding: 0;
				+ .item {
					margin-left: 10px; }
				&:last-child {
					margin-right: 15px; } } } }
	.product-col {
		height: 100%;
		white-space: normal;
		figure {
			.product-img {
				height: 150px; }
			figcaption {
				.product-price {
					min-height: 30px; } } } } }

.home-brand {
	margin-bottom: 20px;
	@include pos(rel,null);
	@include media-breakpoint-down(sm) {
		@include bor-rad(0);
		margin: 0px -15px 20px; }
	.brand-wrap {
		padding: 15px;
		background: $white;
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		@include media-breakpoint-down(sm) {
			padding: 10px 0px 0; } }
	.brand-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px;
		@include media-breakpoint-down(md) {
			flex-direction: column;
			height: 200px;
			overflow: auto;
			overflow-y: hidden; }
		@include media-breakpoint-down(sm) {
			height: 150px; }
		&::-webkit-scrollbar-track {
			background-color: transparent; }
		&::-webkit-scrollbar {
			width: 0px;
			background-color: transparent; }
		&::-webkit-scrollbar-thumb {
			background-color: transparent; }
		.item {
			width: calc(100% / 8);
			@include media-breakpoint-down(md) {
				width: auto;
				height: 49.5%; } } }
	.brand-item {
		@include flexbox;
		height: 80px;
		align-items: center;
		align-content: center;
		justify-content: center;
		border: 1px solid transparent;
		@include bor-rad(5px);
		@include transition(all,0.5s);
		@include media-breakpoint-down(md) {
			width: 160px;
			height: 100%; }
		@include media-breakpoint-down(sm) {
			width: 110px; }
		img {
			max-width: 70%;
			max-height: 70%;
			@include transition(all,0.5s);
			@include media-breakpoint-down(sm) {
				max-width: 80%;
				max-height: 80%; } }
		&:hover {
			@include media-breakpoint-up(xl) {
				border: 1px solid darken($white,10%);
				img {
					@include transform(scale3d(1.2,1.2,1)); } } } } }

.home-product {
	@include pos(rel,null);
	margin-bottom: 40px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 5px; }
	&.pc {
		@include media-breakpoint-down(md) {
			display: none; } }
	&.mobile {
		display: none;
		@include media-breakpoint-down(md) {
			display: block; } }
	.home-title {
		margin-bottom: 30px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 15px; } }
	.product-list {
		@include media-breakpoint-up(lg) {
			background: darken($white,5%);
			padding: 15px 15px 5px;
			@include bor-rad(5px); }
		.row {
			&.no-gutters {
				margin: 0px -5px !important;
				> [class*="col-"] {
					padding: 0px 5px !important;
					margin-bottom: 10px; } } }
		.product-col {
			border: none;
			background: $white;
			@include media-breakpoint-down(sm) {
				background: none;
				border: 1px solid darken($white,5%); } } }
	.product-category {
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		@include bor-rad(5px);
		background: $white;
		overflow: hidden;
		@include media-breakpoint-down(lg) {
			box-shadow: none; }
		@include media-breakpoint-down(sm) {
			box-shadow: none;
			@include bor-rad(0);
			overflow: auto;
			margin: 0px -15px; } }
	.category-nav {
		margin-bottom: 10px;
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 3;
			@include fs(0);
			margin-top: 5px;
			&:before {
				content: '';
				width: 36px;
				height: 36px;
				@include bor-rad(50px);
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center;
				@include fs(14rem);
				border: 1px solid darken($white,5%); }
			&:hover {
				&:before {
					background: $blue;
					border-color: $blue;
					color: $white; } } }
		.slick-prev {
			left: 15px;
			&:before {
				content: '\f177'; } }
		.slick-next {
			right: 15px;
			&:before {
				content: '\f178'; } }
		.slick-dots {
			text-align: center;
			@include pos(abs, null 0 0 0);
			// visibility: hidden
			li {
				display: inline-block;
				width: 6px;
				height: 6px;
				@include bor-rad(50px);
				background: darken($white,20%);
				+ li {
					margin-left: 5px; }
				button {
					display: none; }
				&.slick-active {
					background: $blue; } } }
		.item {
			.category-name {
				text-align: center;
				@include fs(16rem);
				color: $black;
				font-weight: 700;
				text-transform: uppercase;
				padding: 15px 0; } } }
	.category-slick {
		.product-list {
			border-top: none; }
		.item {
			@include media-breakpoint-down(sm) {
				padding: 0px 15px; } } }

	.btn-wrap {
		padding-top: 20px; } }

.home-news {
	@include pos(rel,null);
	margin-bottom: 20px;
	@include media-breakpoint-down(sm) {
		@include bor-rad(0); }
	&.home-facebook {
		margin-bottom: 30px; }
	.home-title {
		margin-bottom: 30px; }
	.btn-wrap {
		text-align: center; }
	.btn-viewall {
		@include pos(static, null);
		@extend .btn-outline;
		@include media-breakpoint-down(sm) {
			@include fs(13rem); }
		&:after {
			display: none; } }

	.news-wrap {
		background: $white;
		// +bor-rad(5px)
		overflow: hidden;
		@include media-breakpoint-down(sm) {
			padding: 0;
			box-shadow: none;
			// +bor-rad(0)
			margin: 0px -15px; } }
	.news-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -15px;
		@include media-breakpoint-down(sm) {
			display: block;
			white-space: nowrap;
			overflow: auto;
			overflow-y: hidden;
			padding: 0px 15px;
			margin: 0 0 15px; }
		.item {
			width: 50%;
			padding: 0px 15px;
			@include media-breakpoint-down(md) {
				padding: 0px 15px; }
			@include media-breakpoint-down(sm) {
				display: inline-block;
				vertical-align: top;
				width: 250px;
				padding: 0;
				+ .item {
					margin-left: 5px; } } } }
	.news-item {
		display: block;
		margin-bottom: 15px;
		padding-bottom: 15px;
		// border-bottom: 1px dashed darken($white,10%)
		@include media-breakpoint-down(sm) {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: none; }
		figure {
			@include flexbox;
			flex-wrap: wrap;
			.news-img {
				width: 40%;
				height: 150px;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				overflow: hidden;
				@include bor-rad(5px);
				@include transition(all,0.5s);
				@include media-breakpoint-only(md) {
					height: 110px; }
				@include media-breakpoint-down(sm) {
					width: 100%;
					height: 150px; }
				img {
					min-width: 100%;
					min-height: 100%;
					@include transition(all,0.5s);
					@include media-breakpoint-down(md) {
						object-fit: cover; } } }
			figcaption {
				width: 60%;
				padding-top: 15px;
				padding-left: 15px;
				@include media-breakpoint-down(sm) {
					width: 100%;
					padding: 5px 0px 0;
					white-space: normal; }
				time {
					display: block;
					@include fs(12rem);
					font-weight: 500;
					color: $blue; }
				.news-name {
					@include fs(16rem);
					line-height: 1.3;
					font-weight: 500;
					// text-transform: uppercase
					margin-bottom: 5px;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					@include transition(all,0.5s);
					@include media-breakpoint-down(md) {
						@include fs(14rem);
						@include textover(2); }
					@include media-breakpoint-down(sm) {
						// +fs(13rem)
						@include textover(3); } }
				.news-des {
					@include fs(14rem);
					line-height: 1.4;
					color: darken($white,40%);
					display: -webkit-box;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					@include media-breakpoint-only(md) {
						@include fs(13rem);
						@include textover(2); }
					@include media-breakpoint-down(sm) {
						@include fs(12rem);
						display: none; } } } }
		&:hover {
			figure {
				.news-img {
					overflow: hidden;
					img {
						@include transform(scale3d(1.1,1.1,1)); } }
				figcaption {
					.news-name {
						color: $blue; } } } } } }
