	margin-bottom: 30px;
	@include media-breakpoint-down(sm) {
		@include bor-rad(0); }
	.home-title {
		margin-bottom: 30px; }
	.btn-wrap {
		text-align: center; }
	.btn-viewall {
		@include pos(static, null);
		@extend .btn-outline;
		@include media-breakpoint-down(sm) {
			@include fs(13rem); }
		&:after {
			display: none; } }
	.facebook-wrap {
		background: $white;
		overflow: hidden;
		@include media-breakpoint-down(md) {
			padding: 0; }
		@include media-breakpoint-down(sm) {
			margin: 0px -15px;
			box-shadow: none;
			padding: 0;
			@include bor-rad(0); }
		.fb-list {
			@include flexbox;
			flex-wrap: wrap;
			margin: 0px -10px 30px;
			@include media-breakpoint-down(lg) {
				margin: 0px -5px; }
			@include media-breakpoint-down(md) {
				display: block;
				white-space: nowrap;
				overflow: auto;
				padding: 15px; }
