				padding: 0px 15px; }
			.item {
				padding: 0px 10px;
				width: calc(100% / 5);
				@include media-breakpoint-down(lg) {
					padding: 0px 5px; }
				@include media-breakpoint-down(md) {
					display: inline-block;
					vertical-align: top;
					width: 170px; }
				@include media-breakpoint-down(sm) {
					width: 250px;
					padding: 0;
					+ .item {
						margin-left: 5px; } } }
			.fb-item {
				display: block;
				height: calc(100% - 0px);
				@include bor-rad(5px);
				overflow: hidden;
				background: darken($white,5%);
				@include media-breakpoint-down(md) {
					margin-bottom: 0; }
				figure {
					.fb-img {
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						height: 150px;
						overflow: hidden;
						@include pos(rel,null);
						@include media-breakpoint-down(lg) {
							height: 100px; }
						@include media-breakpoint-down(sm) {
							height: 150px; }
						&:before {
							content: '\f09a';
							background: $white;
							@include fs(16rem);
							width: 32px;
							height: 32px;
							@include bor-rad(100px);
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							color: $facebook;
							@include pos(abs, null null 10px 10px);
							box-shadow: 0px 3px 5px color-rgba($black,0.1); }
						img {
							min-width: 100%;
							min-height: 100%;
							@include media-breakpoint-down(md) {
								object-fit: cover; } } }
					figcaption {
						padding: 15px;
						@include media-breakpoint-down(sm) {
							padding: 10px 15px; }
						time {
							display: inline-block;
							@include fs(13rem);
							color: darken($white,40%);
							margin-bottom: 5px;
							@include media-breakpoint-down(lg) {
								@include fs(12rem); } }
						.fb-des {
							@include fs(14rem);
							line-height: 1.4;
							color: $main;
							display: -webkit-box;
							-webkit-line-clamp: 3;
							-webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							@include transition(all,0.5s);
							@include media-breakpoint-down(lg) {
 }								// +fs(13rem)
							@include media-breakpoint-down(md) {
								white-space: normal;
								height: 0.875rem*3*1.4; } } } }
				&:hover {
					figure {
						figcaption {
							.fb-des {
								color: $blue; } } } } } } } }
.homepage {
	.pages {
		display: none!important; } }
